.tariffs {
  padding-bottom: 120px;

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 40px;

    @include high-tablet {
      gap: 20px;
    }

    @include small-tablet {
      grid-template-columns: repeat(2, 1fr);
    }

    @include mobile {
      grid-template-columns: repeat(1, 1fr);
      gap: 30px;
    }
  }

  &__item {
    padding: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid rgba(20, 16, 36, 0.2);
    border-radius: 20px;

    @include high-desktop {
      padding: 20px;
    }

    &--current {
      background-color: var(--dark-color);
      color: var(--light-color);
      transform: scale(1.1);
      border: none;

      @include high-desktop {
        transform: scale(1);
      }
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 63px;
    font-weight: 600;
    font-size: 30px;
  }

  &__price {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 50px;

    @include high-desktop {
      font-size: 40px;
    }

    @include small-tablet {
      font-size: 30px;
    }

    @include small-tablet {
      font-size: 50px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 61px;
    font-size: 18px;
    text-align: center;
  }

  &__btn {
    margin-top: auto;
    width: 100%;

    @include high-tablet {
      padding: 10px 20px;
    }
  }


}