.header {
  position: absolute;
  left: 0;
  top: 0;
  padding: 30px 0;
  width: 100%;

  @include high-tablet {
    padding: 10px 0;
  }

  &__logo {
    @include small-tablet {
      width: 136px;
      height: 29px;
    }
  }

  &__container {
    @include flex-all-sb;
    gap: 10px;
  }

  &__phone {
    @include high-tablet {
      margin-left: auto;
      font-size: 0;
      position: relative;
      border: 1px solid rgba(255, 255, 255, 0.2);
      border-radius: 10px;
      width: 44px;
      height: 44px;
      background-image: url(../img/phone.svg);
      background-position: center;
      background-repeat: no-repeat;
      background-size: 24px 24px;
    }
  }
}
