.tabs {
  &__nav {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    border-bottom: 1px solid rgba(20, 16, 36, 0.11);
    margin-bottom: 20px;

    @include mobile {
      grid-template-columns: repeat(1, 1fr);
    }

    &-item {
      padding-bottom: 20px;

      @include mobile {
        padding-bottom: 10px;
      }
    }

    &-btn {
      font-size: 20px;
    }

    &-btn--active {
      outline: none;
      color: var(--accent-color);

    }

    &-btn:focus {
      background-color: transparent;
      color: var(--accent-color);
    }
  }




}