.about {
  background-color: var(--light-color);

  &__descr {
    color: var(--dark-color);
    margin: 0;
    font-weight: 400;
    font-size: 30px;
    line-height: 1.6;
    max-width: 891px;
    margin-bottom: 60px;

    @include mobile {
      font-size: 20px;
    }

    &-highlight {
      font-weight: 600;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 40px;

    @include high-desktop {
      grid-template-columns: repeat(2, 1fr);
    }

    @include mobile {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__digit {
    font-weight: 600;
    font-size: 70px;
    color: var(--accent-color);

    @include mobile {
      text-align: center;
      font-size: 60px;
    }
  }

  &__text {
    margin: 0;
    font-size: 18px;
    color: var(--text-color);

    @include mobile {
      text-align: center;
      font-size: 16px;
    }
  }
}