.tooltip {
  margin-left: 10px;
  &__btn {
    @include flex-all-center;

    width: 24px;
    height: 24px;
    border-radius: 50%;
    background-color: rgba(20, 16, 36, 0.08);

    svg {
      width: 2px;
      height: 12px;
      color: var(--dark-color);
    }

    &:focus {
      + .tooltip__txt {
        opacity: 1;
        visibility: visible;
    }
  }

    @include hover {
      + .tooltip__txt {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__txt {
    border-radius: 5px;
    padding: 15px 20px;
    box-shadow: 0 2px 8px 0 rgba(20, 16, 36, 0.1);
    font-weight: 400;
    background: var(--light-color);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }
}
