.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  padding: 25px 54px;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.6;
  background-color: var(--accent-color);
  color: var(--light-color);

  &--dark {
    background-color: var(--dark-color);
    height: 64px;
  }
}