.hero {
  padding-top: 160px;
  min-height: fit-content;
  background-color: var(--dark-color);

  @include small-tablet {
    position: relative;
    z-index: -1;
    padding-top: 100px;
    padding-bottom: 60px;
    
  }


  &__container {
    @include flex-v-center;

    @include small-tablet {
      flex-direction: column;
    }
  }

  &__content {
    max-width: 516px;

    @include small-tablet {
      max-width: 300px;

    }
  }

  &__title {
    margin: 0;
    font-weight: 600;
    font-size: 70px;
    text-transform: uppercase;
    color: var(--light-color);

    @include small-tablet {
      font-size: 30px;
    }

    &-text {
      display: block;
      font-weight: 400;
      font-size: 20px;
      text-transform: initial;
      margin-bottom: 50px;


    }
  }

  &__image {
    transform: translateX(167px);

    @include small-tablet {
      margin-top: 20px;
      width: 273px;
      height: 320px;
      transform: translateX(0);
    }
  }

  &__link {
    @include small-tablet {
      position: absolute;
      bottom: 40px;
      z-index: 1;
      max-width: 300px;
      width: 100%;
    }
  }
}