.form {
  display: flex;
  flex-direction: column;
  width: 100%;
  row-gap: 10px;

  &__label {
    display: flex;flex-direction: column-reverse;
  }
  .just-validate-error-label {
    margin-bottom: 3px;
    color: var(--light-color) !important;
  }

  &__input {
    border-radius: 5px;
    padding: 20px;
    width: 100%;
    height: 64px;
    font-size: 18px;

    &::placeholder {
      font-size: 18px;
      color: rgba(40, 41, 68, 0.7);
    }
  }
}