.footer {
  background-color: var(--dark-color);

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 40px 100px;

    @include small-tablet {
      padding: 40px 20px;
    }

    @include mobile {
      flex-direction: column;
      align-items: center;
      row-gap: 20px;
    }
  }

  &__logo {
    display: inline-block;
    margin-bottom: 5px;

    @include mobile {
      width: 136px;
      height: 29px;
    }
  }

  &__copy {
    color: rgba(255, 255, 255, 0.7);

    @include mobile {
      order: 2;
      font-size: 14px;
    }
  }

  &__left {
    @include mobile {
      display: contents;
    }
  }

  &__phone {
    @include mobile {
      order: 1;
      margin-bottom: 20px;
      font-size: 16px;
    }
  }
}