.nav {
  &__list {
    @include flex;
    column-gap: 40px;

    @include high-tablet {
      @include flex-all-center;

      flex-direction: column;
      align-items: center;
      row-gap: 20px;
      width: 100%;
      height: 100%;
    }
  }

  &__link {
    font-size: 20px;
    color: var(--light-color);

    @include high-tablet {
      font-size: 30px;
      font-weight: 600;
    }
  }

  @include high-tablet {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 500;
    overflow-y: auto;
    width: 100%;
    height: 100vh;
    background-color: var(--dark-color);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }

}

.menu--active {
  opacity: 1;
  visibility: visible;
}