.order {
  padding: 87px 0;
  background-color: var(--accent-color);
  color: var(--light-color);
  background-image: url(../img/procents.svg);
  background-size: cover;
  background-position: bottom center;

  &__container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);

    @include mobile {
      display: flex;
      flex-direction: column;
      align-items: center;
      row-gap: 40px;
    }
  }

  &__left {
    padding-left: 60px;
    grid-column: 5 span;

    @include high-tablet {
      padding-left: 0;
      text-align: center;
    }
  }

  &__right {
    display: flex;
    justify-content: flex-end;
    grid-column: 7 span;
    padding-right: 60px;

    @include high-tablet {
      padding: 0;
      padding-left: 40px;
    }

    @include mobile {
      padding: 0;
      max-width: 500px;
      width: 100%;
    }

    &--popup {
      padding: 0;
    }
  }

  &__form {
    max-width: 500px;

  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 36px;
    line-height: 1.3;

    @include small-tablet {
      font-size: 30px;
    }
  }

  &__descr {
    margin: 0;
    font-size: 20px;
    line-height: 1.3;

    @include small-tablet {
      font-size: 16px;
    }
  }
}