.graph-modal__close {
  right: 40px;
  top: 40px;
  width: auto;
  height: auto;
  font-size: 18px;
  text-align: right;
  color: rgba(20, 16, 36, 0.7);
  background-image: none;

  @include mobile {
    background-image: url(../img/close-dark.svg);
    background-repeat: no-repeat;
    color: transparent;
    top: 20px;
    right: 20px;
    width: 13px;
    height: 13px;
  }
}

.popup {
  padding: 40px;

  @include mobile {
    padding: 20px;
  }

  &__top {
    display: flex;
    column-gap: 40px;
    margin-bottom: 40px;

    @include mobile {
    padding-top: 20px;
    margin-bottom: 20px;
    column-gap: 10px;
    }
  }

  &__name {
    font-weight: 600;
    font-size: 24px;
    margin-bottom: 10px;

    @include mobile {
      font-size: 16px;
    }
  }

  &__post {
    font-size: 18px;
    margin-bottom: 10px;

    @include mobile {
      font-size: 12px;
      line-height: 1.4;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 10px;
    font-size: 20px;

    @include mobile {
      font-size: 14px;
      margin-bottom: 5px;
    }
  }

  &__bottom-content {
    max-height: 354px;
  }

  &__descr {
    margin-bottom: 20px;
  }

  &-order {
    background-color: #a8a4a4;
    width: 500px;
    padding-top: 100px;
  }

}

.simplebar-track.simplebar-vertical {
  border-radius: 40px;
  width: 5px;
  height: 345px;
  background: rgba(20, 16, 36, 0.08) !important;
}

.simplebar-scrollbar:before {
  left: 0;
  right: 0;
  border-radius: 40px;
  width: 5px;
  background-color: var(--dark-color);
  opacity: 1;
}

.graph-modal__container-order {
  position: relative;
  width: 500px;
  margin: auto;
  display: block;
  background-color: #a8a4a4;
  border-radius: 10px;
  padding: 120px 20px 20px;
  cursor: default;
  display: none;
}
