.trainers {
  &__wrapper {
    padding-left: max(15px, calc((100% - var(--content-width)) / 2));
  }

  &__name {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 600;
    font-size: 24px;
  }

  &__post {
    margin: 0;
    margin-bottom: 20px;
    font-size: 18px;
    color: var(--text-color);
  }

  &__btn {
    font-size: 18px;
    color: var(--accent-color);
  }

  .swiper-slide {
    width: 360px;

    @include mobile {
      width: 220px;
      margin-right: 20px !important;
    }
  }

  &__image {
    max-height: 500px;
    margin-bottom: 20px;
    border-radius: 20px;

    @include mobile {
      width: 220px;
    }
  }

  &__scroll {
    border-radius: 40px;
    width: 360px !important;
    height: 5px !important;
    background: rgba(20, 16, 36, 0.08);
  }

  .swiper-scrollbar.swiper-scrollbar-horizontal {
    position: sticky;
    left: 0%;
  }

  .swiper-scrollbar-drag {
    background-color: var(--dark-color);
  }

  &__content {
    margin-bottom: 77px;
  }

  &__nav {
    @include flex-all-sb;

    max-width: var(--content-width);
  }

  &__btns {
    @include flex-v-center;

    column-gap: 10px;

    @include mobile {
      display: none;
    }
  }

  &__slider-btn {
    @include bg-cover;

    width: 40px;
    height: 40px;

    &--prev {
      background-image: url("../img/prev.svg");
    }

    &--next {
      background-image: url("../img/next.svg");
    }
  }
}