.benefits {
  padding-block: 100px;
  background-color: var(--gray-color);

  @include mobile {
    padding-block: 50px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 40px;

    @include high-desktop {
      grid-template-columns: repeat(2, 1fr);
    }

    @include mobile {
      grid-template-columns: repeat(1, 1fr);
      gap: 30px;
    }
  }

  &__item {
    @include mobile {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }

  &__subtitle {
    display: inline-flex;
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 24px;

    @include mobile {
      margin: 0;
    }
  }

  &__descr {
    max-width: 360px;
    font-size: 18px;
    color: var(--text-color);

    @include mobile {
      text-align: center;
    }
  }
}
